import React, { useEffect, useRef, useState } from 'react'
import app, { storage } from '../../../firebase';
import Modal from 'react-modal';

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
};
function Prod_up() { 
    const [width, setWidth] = useState("200")
    const [height, setHeight] = useState("200")
    const [image, setImage] = useState([])
    const[thumb, seThumb] = useState(null);
    const[url, setUrl] = useState([]);
    const [imagef, setImagef] = useState([])
    const [modalIsOpen2, setIsOpen2] = useState(false);
    const titleRef = useRef()
    const textRef = useRef()
    const tagRef = useRef()
    const numRef = useRef()
    const priceRef = useRef()
    const[catlist, setCatlist] = useState([]);
    const[user, setUser] = useState("");
    var key = app.database().ref().push().key;
    let subtitle;

    useEffect(() => {
      app.auth().onAuthStateChanged(user => {
          if (user) {
            setUser(user)
          }
         
      })
     
  }, [])
  
  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = '#000';
  }

  function closeModal() {
    setIsOpen2(false);
  }
    const onImagechange= (event) =>
    {
        if (event.target.files && event.target.files[0]) {
            setImage(oldArray => [...oldArray,URL.createObjectURL(event.target.files[0])]);
            setImagef(event.target.files[0])
            const uploadTask = storage.ref(`images/${key}.png`).put(event.target.files[0]);
            uploadTask.on('state_changed', 
            (snapshot) => {
              // progrss function ....
              const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100);
            }, 
            (error) => {
                 // error function ....
              console.log(error);
              
            }, 
          () => {
            storage.ref(`images/${key}.png`).getDownloadURL().then((url) => {
              if (thumb==null) {
                  seThumb(url) 
              }
              setUrl(oldArray => [...oldArray, url])
            
          })
          }
          );
          }
    }
    const handleSubmit =()=>{

      app.database().ref("posts/"+user.uid).child(key).set({
          img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
      })
      app.database().ref("all/").child(key).set({
        img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
    })
      
      if (catlist[0]!== null) {
        app.database().ref(catlist[0]+"/").child(key).set({
          img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
      })
      }
      if (catlist[1]!== null) {
        app.database().ref(catlist[1]+"/").child(key).set({
          img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
      })
      }
      if (catlist[2]!== null) {
        app.database().ref(catlist[2]+"/").child(key).set({
          img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
      })
      }
      if (catlist[3]!== null) {
        app.database().ref(catlist[3]+"/").child(key).set({
          img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
      })
      }
      if (catlist[4]!== null) {
        app.database().ref(catlist[4]+"/").child(key).set({
          img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
      })
      }
      if (catlist[5]!== null) {
        app.database().ref(catlist[5]+"/").child(key).set({
          img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
      })
      }
      if (catlist[6]!== null) {
        app.database().ref(catlist[6]+"/").child(key).set({
          img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
      })
      }
      if (catlist[7]!== null) {
        app.database().ref(catlist[7]+"/").child(key).set({
          img: thumb,
          price: priceRef.current.value,
          title: titleRef.current.value,
          search: titleRef.current.value.toUpperCase(),
          desc: textRef.current.value,
          id: key,
          user_id:user.uid,
          categ: catlist,
          url: url
      })
      }
      setIsOpen2(true);
  }
    return (
        <div style={{margin:"1rem"}}>
            <h3>Upload to Product Shop</h3>
            <label className='form-label' >Title</label>
          <input
            className='form-input'
            type='text'
            placeholder=' Enter Title'
            style={{width:"10rem"}}
            ref={titleRef}
            required
          />
          <div>
              <div style={{display:"inline-block"}}>
              <p className='form-label' >Description</p>
          <textarea
            className='form-input'
            type='text'
            placeholder=' Enter Description'
            style={{height:"10rem",width:"40rem"}}
           ref={textRef}
            required
          />
              </div>
              <div style={{display:"inline-block", verticalAlign:"top"}}>
              <p >Tags</p>
          <input
            type='text'
            placeholder=' Enter Tags'
            ref={tagRef}
            required
          />
           <p>Photography</p>
          <select  style={{display:"block"}}>
              <option value="Portrait">Portrait</option>
              <option value="Personal">Personal</option>
              <option value="Wedding">Wedding</option>
            </select>
            <p>Standard Photographic Sizes-R-series</p>
          <select  style={{display:"block",marginBottom:"2rem"}}>
              <option value="4R 4X6 102X1">4R 4X6 102X1</option>
              <option value="5R 5X7 127X1">5R 5X7 127X1</option>
              <option value="6R 6X8 152X2">6R 6X8 152X2</option>
            </select>
              </div>
          </div>
          <div >
          <div style={{border:"0.5px solid black",padding:"30px"}}>
          <label for="file-upload" className="custom-file-upload">
     Add File
</label>
<input id="file-upload" type="file" className='img_up' onChange={onImagechange}/>

<div>
    <div style={{display:"inline-block"}}>
    <img src={image[0]|| 'http://via.placeholder.com/500x300'} 
    alt="logo" width={width} height={height}  style={{margin:"0.5rem"}}/>
    </div>
    <div style={{display:"inline-block"}}>
    <img src={image[1]|| 'http://via.placeholder.com/500x300'}
     alt="logo"  style={{margin:"0.5rem"}} width={width} height={height}/>
    </div>
    <div style={{display:"inline-block"}}>
    <img src={image[2]|| 'http://via.placeholder.com/500x300'}
     alt="logo"  style={{margin:"0.5rem"}} width={width} height={height}/>
    </div>
    <div style={{display:"inline-block"}}>
    <img src={image[3]|| 'http://via.placeholder.com/500x300'} 
    alt="logo"  style={{margin:"0.5rem"}} width={width} height={height}/>
    </div>
    <div style={{display:"inline-block"}}>
    <img src={image[4]|| 'http://via.placeholder.com/500x300'} 
    alt="logo"  style={{margin:"0.5rem"}} width={width} height={height}/>
    </div>
</div>
          </div>
        
<p className='form-label' >Price</p>
          <input
            type='number'
            placeholder=' Enter Price'
            ref={priceRef}
            required
          />
           <p className='form-label' >Quantity</p>
          <input
            type='number'
            placeholder=' Enter Price'
            ref={numRef}
            required
          />
          <p>Color</p>
          <select  style={{display:"block",marginBottom:"2rem"}}>
              <option value="Red">Red</option>
              <option value="Green">Green</option>
              <option value="Blue">Blue</option>
              <option value="Yellow">Yellow</option>
              <option value="Pink">Pink</option>
            </select>
            <button className='with-btn' onClick={handleSubmit}>Post </button>
            <Modal
        isOpen={modalIsOpen2}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <h1>successful!!</h1>
      </Modal>
          </div>
          
        </div>
    )
}

export default Prod_up
