import React, { useEffect, useRef, useState } from 'react'
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';
import { useAuth } from '../../../contexts/AuthContext';
import app, { storage } from '../../../firebase';
import Modal from 'react-modal';

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
};
function Photo_gal() {
    const [value,setValue] = useState("")
    const [title,setTitle] = useState("")
    const [image, setImage] = useState(null)
    const [imagef, setImagef] = useState(null)
    const { currentUser } = useAuth()
    var Uid = currentUser.uid;
    var key = app.database().ref().push().key;
    const[list, setList] = useState([]);
    const titleRef = useRef()
    let subtitle;
    const [modalIsOpen, setIsOpen] = useState(false);

    useEffect(() => {
      app.database().ref("gallery/"+Uid).orderByChild("title").on("value", snapshot => {
        let memberlist = [];
        snapshot.forEach(snap => {
            memberlist.push(snap.val());
        });
        setList(memberlist)
      });
    }, [])
    
    function handleChange(event) {
        setValue(event.target.value)
      }
      const onImagechange= (event) =>
      {
          if (event.target.files && event.target.files[0]) {
              setImage(URL.createObjectURL(event.target.files[0]));
              setImagef(event.target.files[0])
            }
      }

      async function handleSubmit2(e){
        e.preventDefault();
   
        const uploadTask = storage.ref(`images/${key}.png`).put(imagef);
      uploadTask.on('state_changed', 
      (snapshot) => {
        // progrss function ....
        const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100);
      }, 
      (error) => {
           // error function ....
        console.log(error);
      
      }, 
    () => {
        // complete function ....
        storage.ref('images').child(key+".png").getDownloadURL().then(url => {
            const gal = app.database().ref("gallery/"+Uid+"/"+titleRef.current.value.toUpperCase());
            const galData = app.database().ref("galdata/"+Uid+"/"+titleRef.current.value.toUpperCase()).child(key);
            gal.on('value', snapshot => {
            
             if (!snapshot.exists()) {
               gal.update({
                 title:titleRef.current.value.toUpperCase(),
                 img: url
               })
               galData.update({
                title:titleRef.current.value.toUpperCase(),
                img: url
              })
             }else
             {
              galData.update({
                title:title,
                img: url
              })
             }
               
          })
        })
        
    });
       
        
      }
      function openModal() {
        setIsOpen(true);
      }
    
      function afterOpenModal() {
        // references are now sync'd and can be accessed.
        subtitle.style.color = '#000';
      }
    
      function closeModal() {
        setIsOpen(false);
      }
    return (
        <div style={{margin:"4rem"}}>
        <div style={{ display:"inline-block"}}>
            <h1 style={{display:"inline-block"}}>My Gallery</h1>
        </div>
        <div style={{margin:"0 10rem", display:"inline-block"}}>
        <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <h2 ref={(_subtitle) => (subtitle = _subtitle)}>Upload</h2>
        <div >
    <div>
                <b>Cover Photo</b>
                <div style={{display:"inline", float:"right"}}>
                <input type="file" onChange={onImagechange}/>
            </div>
            </div>
            <img src={image || 'http://via.placeholder.com/200x100'} alt="logo" width="200" height="100"/>
            
    </div>
    <div className='form-inputs'>
            <label className='form-label'>Gallery Title</label>
            <input
              className='form-input'
              type='text'
              name='title'
              placeholder='Enter your gallery title'
              ref={titleRef}
            />
            
          </div>
          <button className='with-btn' onClick={handleSubmit2}>Upload</button>
        <button  className='with-btn' onClick={closeModal}>close</button>
      </Modal>
           
         <button className='with-btn' onClick={openModal}>Add To Gallery</button>
 
        </div>
            
            <div style={{display:"inline-block"}}>
            <label >
            <select className="form-input" value={value} onChange={handleChange}>
              <option value="Select Category">Select Category</option>
            </select>
          </label>
            </div>
            <div>
            {list.map(data => {
                
                return (
                    <div style={{display:"inline-block", margin:"1rem 2rem",backgroundColor:"#ECECEC"}}>
                    <p style={{ marginTop:"1rem",fontSize:"13px"}}><b>{data.title}</b></p>
                   <img src={data.img || 'http://via.placeholder.com/300x200'} alt="logo" width="300" height="200" />
                   </div>
                );
               
                })}
            </div>
        </div>
    )
}

export default Photo_gal
