import React from 'react'

function Photo_dash() {
    return (
        <div>
                <div style={{display:"inline-block", backgroundColor:"#C8A2C8",margin:"2rem 2rem"}}>
        <p style={{fontSize:"18px", margin:"1rem"}}> <i class="fas fa-book"></i> Total Booking</p>
        <p style={{fontSize:"20px", margin:"1rem"}}> <b>0</b></p>
        </div>
        <div style={{display:"inline-block", backgroundColor:"#C8A2C8",margin:"0 2rem"}}>
        <p style={{fontSize:"18px", margin:"1rem"}}><i class="fas fa-dollar-sign"></i> Total Revenue</p>
        <p style={{fontSize:"20px", margin:"1rem"}}> <b>0</b></p>
        </div>
        <div style={{display:"inline-block", backgroundColor:"#C8A2C8",margin:"0 2rem"}}>
        <p style={{fontSize:"18px", margin:"1rem"}}><i class="fas fa-check-circle"></i> Total booking for current month</p>
        <p style={{fontSize:"20px", margin:"1rem"}}> <b>0</b></p>
        </div>
        <div style={{display:"inline-block", backgroundColor:"#C8A2C8",margin:"0 2rem"}}>
        <p style={{fontSize:"18px", margin:"1rem"}}> <i class="fas fa-exclamation"></i> Upcoming Bookings</p>
        <p style={{fontSize:"20px", margin:"1rem"}}> <b>0</b></p>
        </div>

        <div>
        <div style={{display:"inline-block", backgroundColor:"#C8A2C8",margin:"2rem 2rem",width:"20rem"}}>
        <p style={{fontSize:"18px", margin:"1rem"}}>Upcoming</p>
        <p style={{fontSize:"20px",margin:"1rem"}}> You have no bookings....</p>
        </div>
        <div style={{display:"inline-block", backgroundColor:"#C8A2C8",margin:"2rem 2rem"}}>
        <p style={{fontSize:"18px", margin:"1rem"}}>Bookings By month Report</p>
        <p style={{fontSize:"20px",margin:"1rem"}}> No reports yet...</p>
        </div>
        
             </div>
        </div>
    )
}

export default Photo_dash
