import React, { useEffect, useState } from 'react'
import AliceCarousel from 'react-alice-carousel'
import Footer from './Footer/Footer'
import NavHome from './Navbar/NavHome'
import blog1 from "./Images/blog1.jpg"
import blog2 from "./Images/blog2.jpg"
import "./Form.css"
import app from '../firebase'
import { useNavigate } from 'react-router-dom'

function Blogs() {
    const[list, setList] = useState([]);
    const history = useNavigate()
    useEffect(() => {
        app.database().ref("blog/").orderByChild("likes").on("value", snapshot => {
          let memberlist = [];
          snapshot.forEach(snap => {
              memberlist.push(snap.val());
          });
          setList(memberlist)
        });

        
      }, [])
    
    return (
        <div>
        <NavHome></NavHome>
              <AliceCarousel autoPlay autoPlayInterval="3000" infinite disableButtonsControls>
      <img src={blog1} className="sliderimg"/>
      <img src={blog2} className="sliderimg"/>
</AliceCarousel>
<div>
<h2 style={{textAlign:"center"}}>Most Popular</h2>
{list.map(data => {
                
                return (
                   <div className="dashboard-pro" style={{margin:"2rem auto"}} onClick={()=>{history("/blog_view/"+data.id)}}>
                   <img src={data.img || 'http://via.placeholder.com/400x300'} alt="logo" width="100" height="100" style={{display:"inline-block",margin:"0 1rem"}}
                    onClick={()=>{{history("/blog_view/"+data.id)}}}/>
                   <div style={{display:"inline-block", margin:"0 4rem", verticalAlign:"top"}} onClick={()=>{{history("/blog_view/"+data.id)}}}>
                   <p style={{ marginTop:"1rem"}} onClick={()=>{{history("/blog_view/"+data.id)}}}><b>{data.title}</b></p>
                   <p style={{marginTop:"1rem"}}  onClick={()=>{{history("/blog_view/"+data.id)}}}>By {data.author} {data.date}</p>
                   <p style={{textAlign:"left",width:"700px"}} onClick={()=>{{history("/blog_view/"+data.id)}}}>{data.text}</p>    
                   </div>
                   </div>
                );
               
                })}
</div>
<Footer/>
        </div>
    )
}

export default Blogs
