import React from 'react'

function dash_mess() {
    return (
        <div>
            <h1>My Messages</h1>
            <button className='with-btn' style={{margin:"3rem", display:"block", width:"20rem"}}>
            <i class="fas fa-envelope"></i> Compose New Message
            </button>
            <button className='with-btn' style={{margin:"3rem", display:"block", width:"20rem"}}>
            <i class="fas fa-envelope"></i> Received
            </button>
            <button className='with-btn' style={{margin:"3rem", display:"block", width:"20rem"}}>
            <i class="fas fa-envelope"></i> Sent
            </button>
        </div>
    )
}

export default dash_mess
