import React, { useEffect, useRef, useState } from 'react'
import { useParams } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import app from '../firebase';
import Footer from './Footer/Footer'
import NavHome from './Navbar/NavHome'

function BlogView() {
    let { Id } = useParams();
    const[img, setImg] = useState("");
    const[text, setText] = useState("");
    const[author, setAuthor] = useState("");
    const[date, setDate] = useState("");
    const[title, setTitle] = useState("");
    var [name,setName] = useState("")
    var [image,setImage] = useState("")
    var key = app.database().ref().push().key;
    const comRef = useRef()
    const { currentUser } = useAuth()
    var Uid = currentUser.uid;
    let myCurrentDate = new Date()
    let dat = myCurrentDate.getDate();
    let month = myCurrentDate.getMonth() + 1;
    let year = myCurrentDate.getFullYear();
    const[list, setList] = useState([]);

    useEffect(() => {
      app.database().ref("blog/"+Id+"/img").on("value", snapshot => {
        setImg(snapshot.val())
       });
       app.database().ref("blog/"+Id+"/text").on("value", snapshot => {
        setText(snapshot.val())
       });
       app.database().ref("blog/"+Id+"/author").on("value", snapshot => {
        setAuthor(snapshot.val())
       });
       app.database().ref("blog/"+Id+"/date").on("value", snapshot => {
        setDate(snapshot.val())
       });
       app.database().ref("blog/"+Id+"/title").on("value", snapshot => {
        setTitle(snapshot.val())
       });
       app.database().ref().child('users/'+Uid+'/username').on('value', snapshot => {
            
        setName(snapshot.val()) 
        
       app.database().ref().child('users/'+Uid+'/profile/url/').on('value', snapshot => {
            
            setImage(snapshot.val()) 
            
          })  
    })
    app.database().ref("blog/"+Id+"/comments/").on("value", snapshot => {
        let memberlist = [];
        snapshot.forEach(snap => {
            memberlist.push(snap.val());
        });
        setList(memberlist)
      });

    }, [])
    const handleComment=()=>{
        app.database().ref("blog/"+Id+"/comments/"+key).update({
            text: comRef.current.value,
            name:name,
            img: image,
            date:`${year}-${month<10?`0${month}`:`${month}`}-${dat}`
            })
    }
    return (
        <div>
            <NavHome></NavHome>
<div >
<img src={img|| 'http://via.placeholder.com/400x300'} alt="logo" width="100%" height="400"/>
</div>
<div style={{margin:"0 1rem",textAlign:"center"}}>
<h1 >{title}</h1>
</div>
<div style={{margin:"3rem"}}>
<b>By {author} {date}</b>
<p>{text}</p>
</div>

<div style={{margin:"3rem"}}>
    <h2>Leave a comment</h2>
    <h4>{list.length} Comments</h4>
    <hr/>
    <label className='form-label' >Add a comment</label>
          <textarea
            className='form-input'
            type='text'
            placeholder=' Add a comment'
            style={{height:"10rem",borderWidth:"1",borderColor:"black"}}
           ref={comRef}
         
            required
          />
          <button onClick={handleComment}>Submit</button>
    <div>
    {list.map(data => {
                
                return (
                   <div className="dashboard-pro" style={{margin:"2rem auto"}} >
                   <img src={data.img || 'http://via.placeholder.com/400x300'} alt="logo" width="50" height="50" style={{display:"inline-block"}}/>
                   <div style={{display:"inline-block", verticalAlign:"top"}} >
                   <p style={{ marginTop:"1rem"}}><b>{data.name}</b></p>
                   <p style={{marginTop:"1rem"}}>{data.text}</p>
                   <p style={{textAlign:"left"}} >Posted on {data.date}</p>    
                   </div>
                   </div>
                );
               
                })}
    </div>
    <hr/>
    <h2>Related posts</h2>
</div>
            <Footer/>
        </div>
    )
}

export default BlogView
