import React from 'react'

function Cust_prof() {
    return (
        <div style={{margin:"2rem"}}>
            <h2>Profile</h2>
            <div>
                <input type="radio" value="Male" name="gender"  /> Edit Profile
            </div>
            <div>
                <input type="radio" value="Female" name="gender" /> Interests
            </div>
            <div style={{marginTop:"2rem"}}>
             <button className='with-btn'>Modify</button>   
            </div>
            
        </div>
    )
}

export default Cust_prof
