import React, { useEffect, useState } from 'react'
import Calendar from 'react-calendar';
import Modal from 'react-modal';
import { useAuth } from '../../../contexts/AuthContext';
import app from '../../../firebase';
import moment from 'moment';
import { TimePicker } from 'antd';
import "antd/lib/date-picker/style/index.css"

  const customStyles = {
            content: {
              top: '50%',
              left: '50%',
              right: 'auto',
              bottom: 'auto',
              marginRight: '-50%',
              transform: 'translate(-50%, -50%)',
            },
          };
function Photo_cal() {
    const [modalIsOpen, setIsOpen] = useState(false);
    const [modalIsOpen2, setIsOpen2] = useState(false);
    const [selected, setSelected] = useState("");
    const { currentUser } = useAuth()
    var Uid = currentUser.uid;
    
    const [selectedTime, setSelectedTime] = useState("00:00");
    var[mark3, setMark3] = useState([]);
    var[mark2, setMark2] = useState([]);
    var[mark, setMark] = useState([]);
    const[list, setList] = useState([]);
         
    useEffect(() => {

     
        app.database().ref("calender/"+Uid+"/un/").on("value", snapshot => {
          let unavail = [];
          snapshot.forEach(snap => {
            unavail.push(snap.val());
          });
      
          unavail.map(data => { 
             
              setMark([
              moment( data.date).format('DD-MM-YYYY')]) 
          })
        });
        app.database().ref("calender/"+Uid+"/bk/").on("value", snapshot => {
          let booked = [];
          snapshot.forEach(snap => {
            booked.push(snap.val());
          });
          
          booked.map(data2 => { 
             
              setMark2([
              moment( data2.date).format('DD-MM-YYYY')]) 
          })
         
        });
        app.database().ref("calender/"+Uid+"/sl/").on("value", snapshot => {
          let slot = [];
          snapshot.forEach(snap => {
            slot.push(snap.val());
          });
         
          slot.map(data2 => { 
             
              setMark3([
              moment( data2.date).format('DD-MM-YYYY')]) 
          })
         
        });
  }, [])
 
  function getlist() {
       app.database().ref("calender/"+Uid+"/sl/"+selected+"/times").on("value", snapshot => {
    let slot_time = [];
    snapshot.forEach(snap => {
      slot_time.push(snap.val());
    });
   setList(slot_time)
  });     
  }

    function afterOpenModal() {
           
    }
  
    function closeModal() {
      setIsOpen(false);
    }
    function closeModal2() {
      setIsOpen2(false);
    }
    function handleUnavail() {
      app.database().ref("calender/"+Uid+"/un/"+selected).update({
        date: selected
        })
        
          setIsOpen(false);
    }
    function handleBook() {
      app.database().ref("calender/"+Uid+"/bk/"+selected).update({
        date: selected
        })
    
        setIsOpen(false);
  }
  function handleSlop2() {
    var key = app.database().ref().push().key;
    app.database().ref("calender/"+Uid+"/sl/"+selected).update({
      date: selected
      })
      app.database().ref("calender/"+Uid+"/sl/"+selected+"/times/"+key).update({
        time: selectedTime
        })
  }
  function handleSlop() {
    setIsOpen(false); 
      setIsOpen2(true); 
      getlist()
  }
    const handleChange = value => {
        setIsOpen(true);
        setSelected(value)
    }
    return (
        <div style={{margin:"0 10rem"}}>
            <h1>Calendar</h1>
            <div style={{display:"inline-block"}}>
                <Calendar style={{margin:"0 auto"}}
                    onChange={handleChange}
                    tileClassName={({ date, view }) => {
      if(mark.find(x=>x===moment(date).format("DD-MM-YYYY"))){
       return  'highlight'
      }
      if(mark2.find(x=>x===moment(date).format("DD-MM-YYYY"))){
       return  'highlight1'
      }
      if(mark3.find(x=>x===moment(date).format("DD-MM-YYYY"))){
       return  'highlight2'
      }
    }} 
                />
                
            </div>
            <b style={{margin:"1rem"}}>Available <i class="fas fa-square" style={{color:"white"}}></i></b>
                <b>Booked <i class="fas fa-square" style={{color:"red"}}></i></b> 
                <b style={{margin:"1rem"}}>Slot <i class="fas fa-square" style={{color:"yellow"}}></i></b>  
                <div>
               
      <Modal
        isOpen={modalIsOpen2}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal2}
        style={customStyles}
        contentLabel="Example Modal"
      >
         <label className='form-label-event'>Available times</label>
            <TimePicker
            format="HH:mm a"  
           
    value={moment(selectedTime, "HH:mm a")}
    onSelect={(value) => {
      const timeString = moment(value).format("HH:mm a");
      setSelectedTime(timeString)
}} />

{list.map(data => {
                
                return (
                   <div style={{margin:"2rem auto"}}>
                   <p><b>{data.time}</b></p>
                   </div>
                );
               
                })}
        <button  onClick={handleSlop2}>Add</button>
        <button  onClick={()=>{ setIsOpen2(false)}}>Done</button>
      </Modal>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel=""
      >
          <button  onClick={handleUnavail}>Unavailable</button>
        <button  onClick={handleBook}>Booked</button>
        <button  onClick={handleSlop}>Slot</button>
      </Modal>
            </div>
        </div>
    )
}

export default Photo_cal
