import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../../contexts/AuthContext';
import Footer from '../../Footer/Footer';
import Navbar2 from '../../Navbar/Navbar2';
import Cust_dash from './Cust_dash';
import Cust_prof from './Cust_prof';
import Dash_mess from './Dash_mess';

function Profile2() {
    const { currentUser } = useAuth()
    const history = useNavigate()
    var Uid = currentUser.uid;
    const [op1, setOp1] = useState(false)
    const [op2, setOp2] = useState(false)
    const [op3, setOp3] = useState(false)
    const [op4, setOp4] = useState(false)
    const [op5, setOp5] = useState(false)
    const [op6, setOp6] = useState(false)
    const [op7, setOp7] = useState(false)
    const [op8, setOp8] = useState(false)
    const [op9, setOp9] = useState(false)

    function handleOp1(params) {
        setOp1(false)
        setOp2(false)
        setOp3(false)
        setOp4(false)
        setOp5(false)
        setOp6(false)
        setOp7(false)
        setOp8(false)
        setOp9(false)
      }
      function handleOp2(params) {
        setOp1(true)
        setOp2(false)
        setOp3(false)
        setOp4(false)
        setOp5(false)
        setOp6(false)
        setOp7(false)
        setOp8(false)
        setOp9(false)
      }
      function handleOp3(params) {
        setOp1(false)
        setOp2(true)
        setOp3(false)
        setOp4(false)
        setOp5(false)
        setOp6(false)
        setOp7(false)
        setOp8(false)
        setOp9(false)
      }
      function handleOp5(params) {
        setOp1(false)
        setOp2(false)
        setOp3(false)
        setOp4(true)
        setOp5(false)
        setOp6(false)
        setOp7(false)
        setOp8(false)
        setOp9(false)
      }
      function handleOp7(params) {
        setOp1(false)
        setOp2(false)
        setOp3(false)
        setOp4(false)
        setOp5(false)
        setOp6(true)
        setOp7(false)
        setOp8(false)
        setOp9(false)
      }
    
    return (
        <div>
        <Navbar2 title={"My Dashboard"}/>
        <div style={{margin:" 0 ",border:"1px solid black",display:"flex"}}>
          <div style={{display:"inline-block", margin:"2rem 0 ", textAlign:"start",cursor:"pointer"}}>
          <div onClick={handleOp1} style={{margin:"0 2rem"}}>
          <p style={{fontSize:"20px"}}><b><i class="fas fa-home"></i> Dashboard</b></p>
          </div>
          <div onClick={handleOp2} style={{margin:"0 2rem"}}>
          <p style={{fontSize:"20px"}}><b><i class="fas fa-user-circle"></i> My Profile</b></p>
          </div>
          <div onClick={handleOp3} style={{margin:"0 2rem"}}>
          <p style={{fontSize:"20px"}}><b><i class="fas fa-calendar-alt"></i> My Bookings</b></p>
          </div>
          
          <div onClick={handleOp5} style={{margin:"0 2rem"}}>
          <p style={{fontSize:"20px"}}><b><i class="fas fa-envelope" style={{color:"black"}}></i> Message</b></p>
          </div>
          <div onClick={()=>{history("/settings")}} style={{margin:"0 2rem"}}>
          <p style={{fontSize:"20px"}}><b><i class="fas fa-cogs"></i> Settings</b></p>
          </div>
          </div>
          <div style={{display:"inline-block"}}>
   <div className='vl' style={{height:"800px"}}>
   
   </div>
   </div>
   {
     op1?
      <div style={{display:"inline-block", verticalAlign:"top",width:"50%"}}>
          <Cust_prof/>
     
          </div>
   : op2?
   <div style={{display:"inline-block", verticalAlign:"top",width:"50%"}}>
          <h3>No Bookings Recorded...</h3>
     
          </div>
          :op4?
          <div style={{display:"inline-block", verticalAlign:"top"}}>
          <Dash_mess/>
     
          </div>
          :
          <div style={{display:"inline-block", verticalAlign:"top"}}>
          <Cust_dash/>
     
          </div>
   }
         
         
        </div>
     <Footer/>
     </div>
    )
}

export default Profile2
