import React, { useEffect, useState } from 'react'
import app from '../../../firebase';

function Photo_prof() {
    const [logged, setLogged] = useState(false)
const [name,setName] = useState("")
const [url,setUrl] = useState("")
const [value,setValue] = useState("")


    useEffect(() => {
        app.auth().onAuthStateChanged(user => {
            if (user) {
setLogged(true)
app.database().ref().child('users/'+user.uid+'/username').on('value', snapshot => {
            
    setName(snapshot.val()) 
     
})
app.database().ref().child('users/'+user.uid+'/profile/url/').on('value', snapshot => {
   
   setUrl(snapshot.val()) 
    
})
            }
            else
            {
                setLogged(false)
            }
        })
    }, [])
   function handleChange(event) {
        setValue(event.target.value)
      }
    return (
        <div style={{marginLeft:"10rem"}}>
             <div style={{textAlign:"center"}}>
                   <img src={url ||"https://img.icons8.com/ios-glyphs/30/000000/user-male-circle.png"} style={{
                     color:"white",borderRadius:"50%", display:"block",width:"100px",height:"100px",lineHeight:"30px"
                     ,margin:"0 auto"}}/>
                    <h3>{name}</h3>
                 </div>
            <div>
            
            <form >
          <label>
          <i class="fas fa-edit" ></i> Edit Profile
            <select className="form-input" value={value} onChange={handleChange}>
              <option value="Bio (About Me)">Bio (About Me)</option>
              <option value="Photo shoot Categories">Photo shoot Categories</option>
            </select>
          </label>
          <div style={{display:"inline-block"}}>
          <label>
          <i class="fas fa-camera" ></i> Equipment
            <select className="form-input" value={{}} onChange={handleChange}>
              <option value=" ">Camera</option>
              <option value=" ">Props</option>
            </select>
          </label>
          </div>
          <div style={{display:"inline-block"}}>
          <label>
          Camera
            <select className="form-input" value={{}} onChange={handleChange}>
              <option value=" ">Nikon</option>
              <option value=" ">Cannon</option>
              <option value=" ">Sony</option>
            </select>
          </label>
          </div>
          <div >
          <label>
           Props
            <select className="form-input" value={{}} onChange={handleChange}>
              <option value=" ">Lights</option>
              <option value=" ">Mirrors</option>
              <option value=" ">Backdrop</option>
            </select>
          </label>
          </div>
          <label>
          <i class="fas fa-upload" ></i> Upload Profile Picture
            <select className="form-input" value={{}} onChange={handleChange}>
              <option value=" ">Edit Profile Picture </option>
              <option value=" ">Remove Profile Picture</option>
            </select>
          </label>
        </form>
            </div>
        </div>
    )
}

export default Photo_prof
