import React from 'react'

function Photo_wal() {
    return (
        <div>
            <h3 style={{margin:"2rem"}}>MY WALLET</h3>

            <h3 style={{margin:"0 2rem",display:"inline-block"}}>BALANCE: £0.00</h3>
            <button className='with-btn' >
                Withdraw
            </button>
            <h4 style={{margin:"2rem"}}>Transaction History</h4>
            <div style={{margin:"0 2rem ",border:"1px solid black", height:"10rem", width:"40rem"}}>
            <p>No Transaction</p>
            </div>
        </div>
    )
}

export default Photo_wal
