import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useAuth } from '../../contexts/AuthContext'
import app from '../../firebase'
import Footer from '../Footer/Footer'
import Navbar2 from '../Navbar/Navbar2'
import Dash_mess from './Options/Dash_mess'
import Photo_blog from './Options/Photo_blog'
import Photo_cal from './Options/Photo_cal'
import Photo_dash from './Options/photo_dash'
import Photo_gal from './Options/Photo_gal'
import Photo_prof from './Options/Photo_prof'
import Photo_up from './Options/Photo_up'
import Photo_wal from './Options/Photo_wal'
import Product_up from './Options/Prod_up'


function Profile() {
    var [image,setImage] = useState("")
    var [name,setName] = useState("")
    var [email,setEmail] = useState("")
    var [phone,setPhone] = useState("")
    var [add1,setAdd1] = useState("")
    var [add2,setAdd2] = useState("")
    var [postc,setPostc] = useState("")
    const { currentUser } = useAuth()
    const history = useNavigate()
    var Uid = currentUser.uid;
    const [op1, setOp1] = useState(false)
    const [op2, setOp2] = useState(false)
    const [op3, setOp3] = useState(false)
    const [op4, setOp4] = useState(false)
    const [op5, setOp5] = useState(false)
    const [op6, setOp6] = useState(false)
    const [op7, setOp7] = useState(false)
    const [op8, setOp8] = useState(false)
    const [op9, setOp9] = useState(false)

    useEffect(() => {
   
        app.database().ref().child('users/'+Uid+'/username').on('value', snapshot => {
          
          setName(snapshot.val()) 
           
      })
      app.database().ref().child('users/'+Uid+'/email').on('value', snapshot => {
          
        setEmail(snapshot.val()) 
         
    })
    app.database().ref().child('users/'+Uid+'/phone').on('value', snapshot => {
          
      setPhone(snapshot.val()) 
       
  })
  app.database().ref().child('users/'+Uid+'/address/add1').on('value', snapshot => {
          
    setAdd1(snapshot.val()) 
     
})
app.database().ref().child('users/'+Uid+'/address/add2').on('value', snapshot => {
          
  setAdd2(snapshot.val()) 
   
})
app.database().ref().child('users/'+Uid+'/address/postc').on('value', snapshot => {
          
setPostc(snapshot.val()) 
 
})
app.database().ref().child('users/'+Uid+'/profile/url/').on('value', snapshot => {
          
setImage(snapshot.val()) 

})
 
  }, [])

function handleOp1(params) {
  setOp1(false)
  setOp2(false)
  setOp3(false)
  setOp4(false)
  setOp5(false)
  setOp6(false)
  setOp7(false)
  setOp8(false)
  setOp9(false)
}
function handleOp2(params) {
  setOp1(true)
  setOp2(false)
  setOp3(false)
  setOp4(false)
  setOp5(false)
  setOp6(false)
  setOp7(false)
  setOp8(false)
  setOp9(false)
}
function handleOp3(params) {
  setOp1(false)
  setOp2(true)
  setOp3(false)
  setOp4(false)
  setOp5(false)
  setOp6(false)
  setOp7(false)
  setOp8(false)
  setOp9(false)
}
function handleOp4(params) {
  setOp1(false)
  setOp2(false)
  setOp3(true)
  setOp4(false)
  setOp5(false)
  setOp6(false)
  setOp7(false)
  setOp8(false)
  setOp9(false)
}
function handleOp5(params) {
  setOp1(false)
  setOp2(false)
  setOp3(false)
  setOp4(true)
  setOp5(false)
  setOp6(false)
  setOp7(false)
  setOp8(false)
  setOp9(false)
}
function handleOp6(params) {
  setOp1(false)
  setOp2(false)
  setOp3(false)
  setOp4(false)
  setOp5(true)
  setOp6(false)
  setOp7(false)
  setOp8(false)
  setOp9(false)
}
function handleOp7(params) {
  setOp1(false)
  setOp2(false)
  setOp3(false)
  setOp4(false)
  setOp5(false)
  setOp6(true)
  setOp7(false)
  setOp8(false)
  setOp9(false)
}
function handleOp8(params) {
  setOp1(false)
  setOp2(false)
  setOp3(false)
  setOp4(false)
  setOp5(false)
  setOp6(false)
  setOp7(true)
  setOp8(false)
  setOp9(false)
}
function handleOp9(params) {
  setOp1(false)
  setOp2(false)
  setOp3(false)
  setOp4(false)
  setOp5(false)
  setOp6(false)
  setOp7(false)
  setOp8(true)
  setOp9(false)
}
function handleOp10(params) {
  setOp1(false)
  setOp2(false)
  setOp3(false)
  setOp4(false)
  setOp5(false)
  setOp6(false)
  setOp7(false)
  setOp8(false)
  setOp9(true)
}
    return (
        <div>
        <Navbar2 title={"My Dashboard"}/>
           <div style={{margin:" 0 ",border:"1px solid black",display:"flex"}}>
             <div style={{display:"inline-block", margin:"2rem 0 ", textAlign:"start",cursor:"pointer"}}>
             <div onClick={handleOp1} style={{margin:"0 2rem"}}>
             <p style={{fontSize:"20px"}}><b><i class="fas fa-home"></i> Dashboard</b></p>
             </div>
             <div onClick={handleOp2} style={{margin:"0 2rem"}}>
             <p style={{fontSize:"20px"}}><b><i class="fas fa-user-circle"></i> My Profile</b></p>
             </div>
             <div onClick={handleOp3} style={{margin:"0 2rem"}}>
             <p style={{fontSize:"20px"}}><b><i class="fas fa-calendar-alt"></i> My Calender</b></p>
             </div>
             <div onClick={handleOp10} style={{margin:"0 2rem"}}>
             <p style={{fontSize:"20px"}}><b><i class="fas fa-images"></i> My Gallery</b></p>
             </div>
             <div onClick={handleOp4} style={{margin:"0 2rem"}}>
             <p style={{fontSize:"20px"}}><b><i class="fas fa-wallet"></i> My Wallet</b></p>
             </div>
             <div onClick={handleOp5} style={{margin:"0 2rem"}}>
             <p style={{fontSize:"20px"}}><b><i class="fas fa-envelope" style={{color:"black"}}></i> Message</b></p>
             </div>
             <div onClick={()=>{history("/blogs")}} style={{margin:"0 2rem"}}>
             <p style={{fontSize:"20px"}}><b><i class="fas fa-rss-square"></i> Blog</b></p>
             </div>
             <div onClick={()=>{history("/settings")}} style={{margin:"0 2rem"}}>
             <p style={{fontSize:"20px"}}><b><i class="fas fa-cogs"></i> Settings</b></p>
             </div>
             <div onClick={handleOp8} style={{margin:"0 2rem"}}>
             <p style={{fontSize:"20px"}}><b><i class="fas fa-upload"></i> Upload</b></p>
             </div>
             <div onClick={handleOp9} style={{margin:"0 2rem"}}> 
             <p style={{fontSize:"20px"}}><b><i class="fas fa-bell"></i> Notifications</b></p>
             </div>
             </div>
             <div style={{display:"inline"}}>
      <div className='vl' style={{height:"100%"}}>
      
      </div>
      </div>
      {
        op1?
         <div style={{display:"inline", verticalAlign:"top",width:"50%"}}>
             <Photo_prof/>
        
             </div>
      : op2?
      <div style={{display:"inline", verticalAlign:"top",width:"50%"}}>
             <Photo_cal/>
        
             </div>
             :op3?
             <div style={{display:"inline", verticalAlign:"top"}}>
             <Photo_wal/>
        
             </div>
             :op4?
             <div style={{display:"inline", verticalAlign:"top"}}>
             <Dash_mess/>
        
             </div>
             :op5?
             <div style={{display:"inline", verticalAlign:"top"}}>
             <Photo_blog/>
        
             </div>
             :op7?
             <div style={{display:"inline", verticalAlign:"top"}}>
             <Product_up/>
        
             </div>
             :op9?
             <div style={{display:"inline", verticalAlign:"top"}}>
             <Photo_gal/>
        
             </div>
             :
             <div style={{display:"inline", verticalAlign:"top"}}>
             <Photo_dash/>
        
             </div>
      }
            
            
           </div>
        <Footer/>
        </div>
    )
}

export default Profile
